<template>
    <div class="mask" v-show="state">
        <div class="alert">
            <div class="alert-text">{{text}}</div>
            <div class="control">
                <div class="cancel" @click="cancel">{{cancelText || '取消'}}</div>
                <div class="confirm" @click="confirm">{{confirmText || '确定'}}</div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'vuejsAlert',
        data() {

            return {
                state: false,
                text: '',
                confirmText: '',
                cancelText: '',
            }
        },
        methods: {

            cancel() {

                this.currentState = false;
            },
            confirm() {
                
                this.currentState = false;
            }
        }
    }
</script>

<style scoped lang="less">
    .mask {
        position: absolute;
        z-index: 1000;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);

        .alert {
            position: absolute;
            left: 50%;
            top: 50%;
            margin-left: -5.4rem;
            margin-top: -3.12rem;
            width: 10.8rem;
            height: 6.25rem;
            border-radius: .5rem;
            background: #fff;
            font-size: .54rem;
            font-weight: bold;
            overflow: hidden;
            text-align: center;
            transform: scale(0.5);

            .alert-text {
                display: table-cell;
                box-sizing: border-box;
                width: 100%;
                height: 4.2rem;
                padding: 0 1.6rem;
                vertical-align: middle;
            }

            .control {
                box-sizing: border-box;
                width: 100%;
                height: 2.05rem;
                border-top: .01rem solid #e1e1e1;
                padding: .42rem 0;

                .confirm {
                    float: left;
                    box-sizing: border-box;
                    width: 50%;
                    height: 100%;
                    line-height: 1.21rem;
                    border-left: .01rem solid #e1e1e1;
                    color: #ff6565;
                }

                .cancel {
                    float: left;
                    box-sizing: border-box;
                    width: 50%;
                    height: 100%;
                    line-height: 1.21rem;
                    color: #777;
                }
            }
        }
    }
</style>


